<template>
  <v-app>
    <transition
      name="fade"
    >
      <div 
        v-if="!entered"
        class="temp-page entrance d-flex flex-column justify-center align-center"
      > 
        <transition
          name="fade"
        >
          <div
            v-if="ci"
            class="text-center"
          >
            <img
              src="./assets/wfis.png"
              alt="Woori Finance Information System"
              width="60%"
            >
          </div>
        </transition>
        <v-overlay 
          :value="!entered"
          opacity="0.4"
        >
        </v-overlay>
      </div>
    </transition>
    <v-main>
      <router-view/>
    </v-main>
  </v-app>
</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      entered: false,
      ci: false,
    }
  },
  created() {
    if (sessionStorage.entered) {
      this.entered = true
    } else {
      sessionStorage.setItem('entered', 'yes')
    }
    setTimeout(() => {
      this.ci = true
    }, 500)
    setTimeout(() => {
      this.ci = false
    }, 2500)
    setTimeout(() => {
      this.entered = true
    }, 3000)
    setTimeout(() => {
      this.$router.push('/mim')
    }, 3500)
  }
};
</script>

<style scoped>
  .temp-page {
    min-width: 100vw;
    min-height: 100vh;
    position: absolute;
  }
  .entrance {
    z-index: 10;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>