<script>
export default {
  data() {
    return {
      current: 0,
      scrollY: 0,
      tictac: null,
      timer: null,
    }
  },
  methods: {
    async comeHere(n) {
      window.scrollTo({ top: window.innerHeight * 0.95 * (n) + 20, behavior: 'smooth' })
    },
    scrollFunc() {
      const vh = window.innerHeight * 0.95
      if (window.scrollY < vh) {
        this.current = 0
      } else if (window.scrollY < vh * 2) {
        this.current = 1
      } else if (window.scrollY < vh * 3) {
        this.current = 2
      } else if (window.scrollY < vh * 4) {
        this.current = 3
      } else if (window.scrollY < vh * 5) {
        this.current = 4
      } else if (window.scrollY < vh * 6) {
        this.current = 5
      } else {
        this.current = 6
      }
    },
    wheelFunc() {
      if (!this.timer) {
        this. timer = setTimeout(() => {
          this.scrollFunc()
          this.timer = null
        }, 150)
      }
    },
    moveTarget(e) {
      if (e.keyCode === 39) {
        this.goDown()
      } else if (e.keyCode === 37) {
        this.goUp()
      } 
    },
    sleep(ms) {
      return new Promise(resolve => {
        setTimeout(resolve(), ms)
      })
    }
  },
  created() {
    window.scrollTo((0,0),{behavior: "smooth"})
    if (this.$route.path === '/aboutus') {
      window.addEventListener('scroll', this.wheelFunc)
      window.__scrollPosition = document.documentElement.scrollTop || 0;
    }
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll)
    document.removeEventListener("keydown", this.moveTarget, false)
  },
  mounted() {
    let mes = document.querySelectorAll('.me')
    let char = 0
    let timer = setInterval(onTick, 50)
    function onTick() {
      mes[char].classList.add('animate__fadeInUp')
      char++
      if (char === mes.length) {
        complete()
        return
      }
    }
    function complete() {
      clearInterval(timer)
      timer = null
    }
  },
}
</script>

