import Vue from 'vue'
import VueRouter from 'vue-router'
import AboutUs from '@/views/AboutUs.vue'
import Ideas from '@/views/Ideas.vue'
import License from '@/views/License.vue'
import Members from '@/views/Members.vue'
import Floor9 from '@/views/Floor9.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
  },
  {
    path: '/mim',
    name: 'Mim',
    component: AboutUs
  },
  {
    path: '/ideas',
    name: 'Ideas',
    component: Ideas
  },
  {
    path: '/license',
    name: 'License',
    component: License
  },
  {
    path: '/members',
    name: 'Members',
    component: Members
  },
  {
    path: '/floor9',
    name: '9thFloor',
    component: Floor9
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
